import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import ListItemSecondaryAction from "@mui/material/ListItemSecondaryAction";

const dispatchedEvents = [];

const SrsMuiListItemSecondaryAction = function (props, ref) {
  return (
    <ListItemSecondaryAction {...props} theme={props.muiTheme}>
      <slot />
    </ListItemSecondaryAction>
  );
};

const styles = `
  /* Custom styles for the MuiListItemSecondaryAction component */
`;

const props = [
  {
    name: "disableSpacing",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiListItemSecondaryAction),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
